#root {
  --main-color: rgb(93, 77, 77);
  --text-color: rgb(199, 199, 199);
}

.App {
  min-height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  overflow: hidden;
  position: relative;
  background-color: var(--main-color);
  color: var(--text-color);
  font-size: 20px;
}

.container {
  max-width: 1600px;
  margin: 0 auto;
  padding: 15px 0;
}

main {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  min-height: 100%;
}

.header {
  text-align: center;
  margin-top: 20px 0 20px;
}

.write__body {
  text-align: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

._input {
  background-color: rgb(67, 67, 67);
  border: 2px solid var(--text-color);
  border-right-width: 1px;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  padding: 10px 20px;
  font-size: 16px;
  color: var(--text-color);
  height: 42px;
  min-height: 42px;
  max-width: 250px;
  width: 100%;
  overflow: hidden;
}

._button {
  background-color: rgb(67, 67, 67);
  border: 2px solid var(--text-color);
  border-left-width: 1px;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  color: var(--text-color);
  padding: 10px 20px;
  font-size: 16px;
  max-width: 120px;
  width: 100%;
  height: 42px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.posts__body {
  max-width: 600px;
  width: 100%;
  margin: 0 auto;
  border-top: 2px solid var(--text-color);
}
.posts__body li {
  border-bottom: 2px solid var(--text-color);
  padding: 10px 0;
  font-size: 26px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  gap: 5px;
}
.posts__body li ._button {
  border: 2px solid var(--text-color);
  -ms-flex-negative: 0;
      flex-shrink: 0;
  border-radius: 5px;
  max-width: 32px;
  height: 32px;
  padding: 0;
  position: relative;
}
.posts__body li ._button::before {
  content: "";
  width: 12px;
  height: 2px;
  position: absolute;
  background-color: var(--text-color);
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.posts__body li ._button::after {
  content: "";
  width: 12px;
  height: 2px;
  position: absolute;
  background-color: var(--text-color);
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}